import { Component } from '@angular/core';
import {EmployeeService} from '../../service/employee.service';

@Component({
  selector: 'edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css'],
  providers: [EmployeeService]
})

export class EditComponent {

  constructor(private service:EmployeeService){}

  userID;
  column_Name;
  value;
  setUserID(event){
    //console.log(userID+" "+column_Name+" "+value);
    this.userID=event.target.value;
    // this.column_Name=column_Name;
    // this.value=value;
    //this.callService();
  }

  setColumnName(event){
    this.column_Name=event.target.value;
  }

  setValue(event){
    this.value=event.target.value;
    //this.callService();
  }

  callService(){

    var send={
        E_id:this.userID
      }

      this.service.checkIfExists(send).subscribe(tasks=>{
        if(tasks==true){
          var editEmp={
              E_id:this.userID,
              column_Name:this.column_Name,
              value:this.value
          }
          this.service.editEmp(editEmp).subscribe(tasks=>{
          });
        }
        else{
          alert("A valid record for this id does not exist");
        }
      });



  }








}



