import { Component } from '@angular/core';
import {EmployeeService} from '../../service/employee.service';

@Component({
  selector: 'add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.css'],
  providers: [EmployeeService]
})

export class AddComponent {
  userID;
  baseSalary;
  retireDed;
  medicalDed;
  stateDed;
  federalDed;
  otherDed;
  checkFlag;


  constructor(private service:EmployeeService){}

  setUserID(event){
    this.userID=event.target.value;
  
  }

  setBaseSalary(event){
    this.baseSalary=event.target.value;
    //console.log(this.userID);
  }

  setRetire(event){
    this.retireDed=event.target.value;
  }

  setMedical(event){
    this.medicalDed=event.target.value;
  }

  setStateTax(event){
    this.stateDed=event.target.value;
  }

  setFederalTax(event){
    this.federalDed=event.target.value;
  }

  setOtherTax(event){
    this.otherDed=event.target.value;
    //console.log(this.otherDed);
  }

  callService(){

    var send={
      E_id:this.userID
    }

    this.service.checkIfExists(send).subscribe(tasks=>{
      if(tasks==false){
        var addEmp={
          E_id:this.userID,
          base_Salary:this.baseSalary,
          retire:this.retireDed,
          medical:this.medicalDed,
          state:this.stateDed,
          federal:this.federalDed,
          others:this.otherDed
        }

      this.service.addEmp(addEmp).subscribe(tasks=>{});
      }
      else{
        alert("A valid record for this id already exists. Update the record");
      }
    });

  }

}



