import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {HttpModule} from '@angular/http';

import { AppComponent } from './app.component';

import { AddComponent } from './Tasks/addEmployee/add.component';
import { DeleteComponent } from './Tasks/delete/delete.component';
import { EditComponent } from './Tasks/editEmployee/edit.component';
import { ViewComponent } from './Tasks/viewEmployee/view.component';

@NgModule({
  declarations: [
    AppComponent,AddComponent,DeleteComponent,EditComponent,ViewComponent
  ],
  imports: [
    BrowserModule,HttpModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



